@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap);
body {
  background: #ffea84;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
  helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  transition: 0.5s all;
}


.home,
.events,
.products,
.faqs {
  padding: 20px 100px 20px 100px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

h6 {
  font-size: 40px;
  margin-bottom: 10px;
}

h8 {
  font-size: 20px;
}

.navbar {
  color: black;
  /* border-bottom: 3px solid #026857; */
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  top: 0;
  transition: 20ms;
  z-index: 5;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  
}

.navbar.active {
  background: black;
  color: white;
}

table, th, td {
  border: 1px solid black;
  border-collapse: collapse;
  
}

  .logo-title {
    cursor: pointer;
    display: inline-block;
    position: relative;
    font-size: 25px;
    margin-left: 25px;
  }

  .logo-title-moved {
    cursor: pointer;
    transition-delay: 37ms;
    transform: translate(200px);  
  }

  .icon-logo {
    display: none;
  }
  
  @media (max-width: 600px) {
    .logo-title {
      display: none;
    }
    
    .logo-title-moved {
        display: none;
    }

    .icon-logo {
        display: inline-block;
        position: relative;
        display: initial;
        font-size: 25px;
        margin-left: 20px;
    }
      
  }
  
  
body {
    color: black;
}

.bxsci {
    font-size: 100px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.scioly {
    font-size: 100px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-bottom: 20px;
}

.desc {
    font-size: 40px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.scioly:hover, .bxsci:hover, .desc:hover {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.rotated {
    transform: rotate(-180deg);
}

.show-answer {
    transform: translateY(-40px);
}

.blob-1 {
    height: 45%;
    position: absolute;
    left: -14%;
}

.blob-4 {
    height: 500px;
    position: absolute;
    left: -14%;
    top: 1000px;
}

.blob-2 {
    height: 500px;
    position: absolute;
    right: -13%;
    bottom: 10%;
}

.blob-3 {
    height: 500px;
    position: absolute;
    right: -12%;
    top: 1200px;
    transform: rotate(60deg);
}

.page {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 400px;
  }


    @media (max-width: 600px) {
    .bxsci{
        font-size: 35px;
    }
    .scioly{
        font-size: 35px;
    }
    .desc{
        font-size: 25px;
    }
    .rotated {
        font-size: 20px;
    }
    .show-answer {
        font-size: 20px;
    }

    .blob-1, .blob-2, .blob-3, .blob-4 {
        display: none;
    }

}
.upcoming-title {
    margin-bottom: 20px;
}

.announcements-desc {
    font-size: 18px;
    font-weight: 200;
    margin-bottom: 10px;
}



.announcements-subtitle {
    font-weight: 200; 
    margin-bottom: 20px;
}

@media (max-width: 600px) {
